<template>
  <v-container>
    <p class="display-2 mb-3">Şiparişler</p>
    <orders></orders>
  </v-container>

</template>

<script>
import Orders from './Orders'
export default {
  components: {
    Orders
  },

  data(){
      return {

      }
  },
  created (){
    firebase.auth().onAuthStateChanged(user => {
      if (!user) {
        this.$router.push('/admin/Login')
      }
    });
  }
}
</script>



