<template>
    <div>
        <v-card>
            <v-card-title>           
            <v-spacer></v-spacer>
            <v-text-field
                append-icon="search"
                label="Ara"
                single-line
                hide-details
                v-model="search"
            ></v-text-field>
            </v-card-title>
        </v-card>
        <v-data-table
                    :headers="headers"
                    :items="orders"
                    class="elevation-1"
                    :search="search"
                    item-key="order_number"
                    >

                    <tr slot="items" slot-scope="props" :keys="props.item.order_number" @click="props.expanded = !props.expanded">                       
                        <td name="orderNo" class="text-xs-left">{{props.item.order_number}}</td>
                        <td  class="text-xs-left" >{{ props.item.name }}</td>
                        <td  class="text-xs-left">{{ props.item.email }}</td>
                        <td class="text-xs-left">{{ props.item.status }}</td>
                    </tr>
                
                <template slot="expand" slot-scope="props">
                    <v-card style="background-color: #EEEEEE" flat>
                        <v-card-text class="body-1">
                            <v-layout row wrap >
                            <v-flex xs12 sm12 md6>
                             
                            <v-list >
                                <v-list-tile class="mb-0 pb-0">                               
                                <v-list-tile-title >Ürünler</v-list-tile-title>                         
                                </v-list-tile>
                                <v-list-tile  v-for="(item,index) in props.item.products.items" :key="index">                                   
                                <v-list-tile-content class="py-0">
                                    <v-list-tile-sub-title>
                                        {{item.quantity}} Adet  {{item.name}} {{item.size}} numara
                                    </v-list-tile-sub-title>                                                                           
                                </v-list-tile-content>                                                                           
                                </v-list-tile>
                            </v-list>
                            </v-flex>
                            <v-flex xs12 sm12 md6>
                                <v-list>
                                     <v-list-tile>
                                
                                    <v-list-tile-title >Adres</v-list-tile-title>
                                                                           
                                                                                                      
                                </v-list-tile>
                                    <v-list-tile>
                                <v-list-tile-content >
                                    <v-list-tile-sub-title>{{props.item.adress}}</v-list-tile-sub-title>                                                                           
                                </v-list-tile-content>                                                                           
                                </v-list-tile>
                                </v-list>
                            </v-flex>
                            </v-layout>   
                        </v-card-text>
                    </v-card>
                </template>
                <template style="padding: 0" slot="no-data">
                <v-alert  :value="true" color="info" icon="info">
                    Mevcut Sipariş Bulunmuyor.
                </v-alert>
            </template>
        </v-data-table>
    </div>
</template>

<script>
import service from '../../service/service'
export default {
  data(){
      return {
          orders: [],
          orderNumbers: [],
          search: '',
          headers: [
            { text: 'Sipariş No', sortable: false, value: 'order_number'},
            { text: 'Ad Soyad',sortable: false, value: 'name' },
            { text: 'Email',sortable: false, value: 'email' },
            { text: 'Sipariş Durumu',sortable: false, value: 'status' },
          ],
      }
  },
  created(){
      service.getOrders().then(res => {
          this.orders = Object.keys( res.val()).map(k => { return  res.val()[k] });
          this.orderNumbers = Object.keys( res.val());
          console.log(this.orderNumbers);
      })
  }
}
</script>

<style>
.application .theme--light.list, .theme--light .list {
    background: inherit;
}
</style>



